<template>
  <section class="services-prices">
    <img src="/static/img/home/7/bg.png" alt="" class="bg">
    <div class="container">
      <h2 class="section__title">Цены на парикхмахерские услуги</h2>
      <div class="block">
        <div v-if="this.$vssWidth > 1170 && !categoriesLoading" class="tabs">
          <div
              :class="[
                  'tab',
                  {'active': category.id === currentCategoryId}
              ]"
              v-for="category in categories"
              :key="category.id"
              @click="currentCategoryId = category.id"
          >
            <span>{{ category.name }}</span>
          </div>
        </div>
        <VueSlickCarousel
            v-if="this.$vssWidth <= 1170 && !categoriesLoading"
            class="tabs"
            :arrows="false"
            :dots="false"
            :draggable="true"
            :swipe="true"
            :variableWidth="true"
            :adaptiveHeight="true"
        >
          <div
              :class="[
                  'tab',
                  {'active': category.id === currentCategoryId}
              ]"
              v-for="category in categories"
              :key="category.id"
              @click="currentCategoryId = category.id"
          >
            <span>{{ category.name }}</span>
          </div>
        </VueSlickCarousel>
        <Loader class="categories-loader" v-if="categoriesLoading"></Loader>

        <div class="tab-item flex-start" v-if="!categoriesLoading && !servicesLoading">
          <div class="img">
            <img :src="getSrc(currentCategory.thumb)" alt="">
            <div class="description">{{currentCategory.description}}</div>
          </div>

          <div class="price-list-wrapper">
            <div class="price-list">
              <PriceCheckbox
                  v-for="service in services"
                  :key="service.id"
                  :id="service.id"
                  :label="service.name"
                  :price="service.price"
                  v-model="service.isChecked"
              ></PriceCheckbox>
            </div>
            <div class="price-list__total-price flex-between align-center">
              <div class="title">Итоговая стоимость</div>
              <div class="price">{{ totalPrice }} руб.</div>
            </div>
          </div>
        </div>
        <Loader class="services-loader" v-if="servicesLoading || categoriesLoading"></Loader>
      </div>
    </div>
  </section>
</template>

<script>
import PriceCheckbox from "@/components/PriceCheckbox";
import VueSlickCarousel from 'vue-slick-carousel'
import Loader from "@/components/Loader";
import api from "@/api"
import {createImageUrl} from "@/utils";

export default {
  name: "PriceList",
  components: {PriceCheckbox, VueSlickCarousel, Loader},
  props: ['category'],
  created() {
    this.getCategories()
  },
  methods: {
    getSrc(src) {
      return createImageUrl(src)
    },
    async getCategories() {
      this.categoriesLoading = true
      this.categories = await api.getServiceCategories(this.category)
      this.currentCategoryId = this.categories[0].id
      this.categoriesLoading = false
    },
    async getServices() {
      this.servicesLoading = true
      this.services = await api.getServicesForCategory(this.currentCategoryId)
      this.servicesLoading = false
    }
  },
  data: () => ({
    categoriesLoading: false,
    servicesLoading: false,
    currentCategoryId: null,
    categories: [],
    services: [],
  }),
  watch: {
    currentCategoryId() {
      if (this.currentCategoryId !== null) {
        this.services.length = 0
        this.getServices()
      }
    }
  },
  computed: {
    currentCategory() {
      return this.categories.find(c => c.id === this.currentCategoryId)
    },
    totalPrice() {
      return this.services.reduce((acc, p) => {
        return p.isChecked ? acc + p.price : acc
      }, 0)
    }
  }
}
</script>

<style scoped>
.categories-loader {
  height: 68px;
  margin-bottom: 64px;
}
.services-loader {
  height: 552px;
  width: 100%;
}
</style>