<template>
<div class="checkbox-wrapper flex-between align-center">
  <input
      :id="`${id}-service`"
      type="checkbox"
      :checked="checked"
      @change="onChange"
  >
  <label :for="`${id}-service`" class="flex-start align-center">
    <span class="box">
      <svg viewBox="0 0 9 7" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M1 3.373L3.374 5.746L8.12 1" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
</svg>
    </span>
    <span class="label">{{label}}</span>
  </label>
  <div class="price">{{price}} руб.</div>
</div>
</template>

<script>
export default {
  name: "PriceCheckbox",
  props: ['value', 'id', 'label', 'price'],
  data () {
    return {
      checked: this.value
    }
  },
  methods: {
    onChange(e) {
      this.checked = e.target.checked
      this.$emit('input', this.checked)
    }
  }
}
</script>

